import { InferActionsTypes } from "./redux-store"


// initialState  ----------------------------------------------------------------------------------------
// 2    initialState
type DialogType = { // Dialog-
	id : number
	name : string
}
type MessageType = {  // Message-
	id : number
	message : string
}
//   initialState
export type InitialStateType = typeof initialState;
// initialState
let initialState = {
	dialogsData: [
		{ id: 1, name: "Ola" },
		{ id: 2, name: 'Masha' },
		{ id: 3, name: 'Katy' },
		{ id: 4, name: 'Lula' },
		{ id: 5, name: 'Berta' },
		{ id: 6, name: 'Boska' }
	] as Array <DialogType>,  // 1   initialState
	messagesData: [
		{ id: 1, message: 'Hallo' },
		{ id: 2, message: 'Tchudfg' },
		{ id: 3, message: 'Gut' }
	] as Array <MessageType>,  // 2   initialState,
};
//-----------------------------------------------------------------------------------------



//  AC-  1  "actions" -------
export const actions = {
	addMessageActionCreator : (newMessageText : string)=>{return {type : 'ADD-MESSAGE', newMessageText} as const
	}
}

type ActionsType = InferActionsTypes<typeof actions>	// Infer  "actions".   ,  -__ctions   {}  
export type AddMessageActionCreatorType = typeof actions.addMessageActionCreator	//   "addMessageActionCreator"



const dialogsReducer = (state = initialState, action : ActionsType) : InitialStateType=>{

	switch(action.type){
		case 'ADD-MESSAGE':
			let newMessage = {
				id: 4, 
				message: action.newMessageText
			};
		
			return {
				...state,
				// newMessageText : (''),
				messagesData : [...state.messagesData,newMessage]
			};

			
		// case UPDATE_NEW_MESSAGE:
		// 		return {
		// 			...state,
		// 			newMessageText : action.newText,
		// 		};	
			

		default:
			return state;
	}
};
export default dialogsReducer;
	