import { instance, APIResponseType, ResultCodesCaptchaEnum, ResultCodesEnum } from './api';


//      login/logout/me- -------------------------

//   "authAPI"...................



//  -Generic"APIResponseType"   "Response"-  api.ts...................
//   "APIResponseType"  "MeResponse"
type MeResponseDataType = {  //   "me()"  "authAPI"
	id: number   //    data: {id : number, email: string, login : string}
	email: string
	login: string
}

//   "APIResponseType"   "LoginResponse"
type LoginResponseDataType = {   //   "login()"  "authAPI"
	userId: number   //    data: {id : number, email: string, login : string}
}
// ................................


export const authAPI = {
	// from HeaderContainer------
	// Promis "me"

	me() {
		return instance.get<APIResponseType<MeResponseDataType>>(`auth/me`, { withCredentials: true, headers: { Cookie: "_ym_uid=169624083757905547; ASP.NET_SessionId=3nswcypinxy4qljkgouywbpt; _ym_d=1712062275; _ym_isad=2; .ASPXAUTH=D370ACB5B05FB4338293F3FDCB504A12899C08AE74CD1F67B8088EF5E13B38397AE4D067D169700680029FD5623D217695AEA3F67695FDFA1CA55CE9F3FA1F22C1C1E42AA5D0F74385B6D6E28039E5F11E46B1E9" } }).then(res => res.data); //c then-    ,then-  - 
	},
	login(email: string, password: string, rememberMe: boolean = false, captcha: null | string = null) {
		return instance.post<APIResponseType<LoginResponseDataType, ResultCodesEnum | ResultCodesCaptchaEnum>>(`auth/login`, { email, password, rememberMe, captcha })
			.then(res => res.data);
	},
	// logout(email : string, password : string, rememberMe : boolean = false) {
	logout() {
		return instance.delete(`auth/login`);
	}
}
