import {applyMiddleware, combineReducers, legacy_createStore as createStore, compose, Action, AnyAction} from 'redux'; 
import profileReducer from './profile-reducer';
import dialogsReducer from './dialogs-reducer';
import sidebarReducer from './sidebar-reducer';
import usersReducer from './users-reducer';
import authReducer from './auth-reducer';
import appReducer from './app-reducer';
import {thunk, ThunkAction, ThunkDispatch } from 'redux-thunk'; //   
import { reducer as formReducer } from 'redux-form';


let rootReducer = combineReducers({
	profilePage : profileReducer,
	dialogsPage : dialogsReducer,
	sidebar : sidebarReducer,
	usersPage : usersReducer,
	auth : authReducer,
	form : formReducer,
	app : appReducer,
});

type RootReducerType = typeof rootReducer; 				// typeof   RootReducerType  (profilePage, ...) State-(AppState)
export type AppStateType = ReturnType<RootReducerType> //ReturnType     <RootReducerType>     AppStateType
//--type AppStateType = typeof appStateObject;
let state : AppStateType; 										//  "state:"     state



//    "InferActionsTypes"   reducer- ----------------------------------------------
// "T extends(...)" -    "(...)"
type PropertiesTypes<T> = T extends {[key:string]: infer U} ? U : never // "infer" -   "U" - AC- 




/**
 * Infer  "actions".   ,  -__ctions   {}  
*/
export type InferActionsTypes<T extends {[key:string]: (...args: any[])=>any}> = ReturnType<PropertiesTypes<T>> //"<T extends..." -    ""





// ThunkCreator- ------------------------------------------------------------------------------------------------
//  "BaseThunkType"    ThunkCreator- ------------------------------------------------------------------------------------------------


//A - ActionsTypes - Action-,    dispatch-  thunk- (A extends Action  Action-  Redux-)
//R -  -  (Promise<void> -   - )
//AppStateType -  State
//unknown - extra 
//ThunkAction - -  "redux-thunk"
export type BaseThunkType<A extends Action, R =Promise<void>> = ThunkAction<R, AppStateType, unknown, A>

//------   "UseDispatch"- -   Redux---------------------
export type AppDispatch = ThunkDispatch<AppStateType, unknown, Action>
//------------------------------------------------------------------------------------------------



// For Redux Dev Tools----------------------------------------------
//  store      thunkMiddleware----------------
//,      ts-
//@ts-ignore
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(rootReducer, composeEnhancers(applyMiddleware(thunk)
));
// ----------------------------------------------

// let store=createStore(reducers, applyMiddleware(thunk)); //    store

//,      ts-
//@ts-ignore
window.Storage = store;
export default store;