//   - "Profile"- 

import React from 'react';
import Profile from './Profile';
import {connect} from 'react-redux';
import {getUserProfile, getStatus, updateStatus, savePhoto, saveProfile} from '../../redux/profile-reducer'
import { compose } from 'redux';
import { NavigateFunction, useLocation, useNavigate, useParams  } from "react-router-dom";
import { useEffect } from "react";
import { AppStateType } from '../../redux/redux-store.js';
import { ProfileType } from '../../types/types';



//  : (2 compose (1 ProfileContainer))
// 2 compose  (1 ProfileContainer)) : 	
//			 "State"- : 			profile, status, authorizedUserId, isAuth
//			-  callback-:	"{getUserProfile, getStatus, updateStatus, savePhoto, saveProfile}"
//			:					"withRouter-"



// "ProfileContainer".    :-----------------------------------------------------------
// "mapStateToProps"
type MapPropsType = ReturnType<typeof mapStateToProps> //,       "MapPropsType"

// "mapDispatchToProps"
type DispatchPropsType = {
	getUserProfile: (userId: number)=>void
	getStatus: (userId: number)=>void
	updateStatus: (status: string)=>void
	savePhoto: (file : File)=>void
	saveProfile: (profile : ProfileType)=>Promise<any>
}


//  "WithRouter"-
type PathParamsType = {
	userId: string,
	navigate: NavigateFunction
}

// Your component own properties
// type PropsType = RouteComponentProps<PathParamsType> & {
// 	someString: string,
// }
//-----------------------------------------------------------------------------------------------
type PropsType = MapPropsType & DispatchPropsType & PathParamsType




class ProfileContainer extends React.Component<PropsType>{

	refreshProfile(){


		
		// match    withRouter
		let userId: number | null = +this.props.userId;
		//   userId
		if (!userId){
			//   userId  
			userId=this.props.authorizedUserId;
			//      userId  
			if (!userId){
				this.props.navigate("/Login")
			}
		}

		if (!userId){
			console.error("UserId should exist")
		} else{
		this.props.getUserProfile(userId);	//    - User-
		this.props.getStatus(userId);
		}
	}


	componentDidMount(){
		this.refreshProfile()
	}

	componentDidUpdate(prevProps: PropsType, prevState: PropsType){
		//    params
		if (this.props.userId !== prevProps.userId){
			this.refreshProfile()
		}
	}


	render(){
		return (
			<div>
				{/*   userId,        */}
				<Profile {...this.props} 				
				isOwner = {!this.props.userId} 
				profile = {this.props.profile} 
				status = {this.props.status} 
				updateStatus = {this.props.updateStatus}
				savePhoto = {this.props.savePhoto}
				saveProfile ={this.props.saveProfile}
				/>
			</div>
		);
	}
}


let mapStateToProps = (state: AppStateType)=>({
	profile : state.profilePage.profile,
	status : state.profilePage.status,
	authorizedUserId : state.auth.userId,
	isAuth : state.auth.isAuth
	
});


//-,   -
function withRouter<WCP extends MapPropsType&DispatchPropsType> (WrappedComponent: React.ComponentType<WCP>) { //"WrappedComponent" -  -
	
	// -,   -: "location", "navigate", "params"  + "userId ", "navigate"
	function ComponentWithRouterProp(props: MapPropsType & DispatchPropsType) {
		let location = useLocation();
		let navigate = useNavigate();
		let params = useParams(); //   "URL"-


		useEffect(() => {
		if (!props.isAuth) {
			navigate("/login");
		}
		}, [props.isAuth, navigate]);

		// "userId"  "URL"-
		let userId = params.userId;

		return (
			<WrappedComponent
			{...props as WCP}
			router={{ location, navigate, params }} 
			userId = {userId}
			navigate = {navigate}
			/>
		);
	}

	return ComponentWithRouterProp;
	}



// compose   -   HOC (High_Order_Component)// HOC - -, .  1 -,    -  ,    - - 
// HOC - -, .  1 -,    -  ,    - - 
export default compose<React.ComponentType>(
	connect(mapStateToProps, {getUserProfile, getStatus, updateStatus, savePhoto, saveProfile}),
	withRouter,
)(ProfileContainer);