import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';
import { connect, Provider } from 'react-redux';
import store, { AppStateType } from './redux/redux-store';


import { initializeApp } from "./redux/app-reducer";
import './App.css';
// import Navbar from './components/Navbar/Navbar';
// import HeaderContainer from './components/Header/HeaderContainer';
import Preloader from './components/common/Preloader/Preloader';
import AppAntD from './AppLayout';




//  : (4 JSApp (3 AppContainer (2 App(1 AppAntD))))
// 2 AppContainer  (1 App)) : "initialize"    "initializeApp"
// 3 JSApp  (2 AppContainer) : "BrowserRouter"-  "store"- 




// lazy -         bandl,     -   
//     . ..      - - 
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));
const UsersPage = React.lazy(() => import('./components/Users/UsersContainer'));
const LoginPage = React.lazy(() => import('./components/Login/Login'));
const Music = React.lazy(() => import('./components/Music/Music'));
const News = React.lazy(() => import('./components/News/News'));
const Settings = React.lazy(() => import('./components/Settings/Settings'));
const ChatPage = React.lazy(() => import('./pages/Chat/ChatPage'));



// props  "App"
type MapPropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = {
  initializeApp: () => void
}






//--------2-----------------------------------------
class App extends Component<MapPropsType & DispatchPropsType>{

  //   , . 
  // reasen- , promise-     
  catchAllUnhandledErrors = (e: PromiseRejectionEvent): any => {
    alert("Some error occurred")
  }

  //    -----------------------------------------

  // componentDidMount   1 ,  - 
  componentDidMount() {
    this.props.initializeApp();

    //     promis-
    window.addEventListener('unhandledrejection', this.catchAllUnhandledErrors)
  };

  //  addEventListener,  -   removeEventListener  
  componentWillUnmount() {
    window.removeEventListener("unhandledrejection", this.catchAllUnhandledErrors);
  };

  render() {
    if (!this.props.initialized) {
      return <Preloader />
    }

    return (
      <AppAntD />
    );
  }
}



const mapStateToProps = (state: AppStateType) => ({
  initialized: state.app.initialized
});




//--------3-----------------------------------------
let AppContainer = connect(mapStateToProps, { initializeApp })(App);



//--------4-----------------------------------------
const JSApp: React.FC = () => {
  return (
    // BrowserRouter  .        
    // HashRouter       
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      {/* Provider  store   CONTEXT,       */}
      <Provider store={store}>
        <AppContainer />
      </Provider>
    </BrowserRouter>
  )
};

export default JSApp;

